.Monitor{
    background-color: buttonface;
    position: fixed;
    top: 2em;
    right: .1em;
    padding: .3em;
    color: gray;
    width: auto;
    z-index: -1;
    border-top: 2px solid #ccc;
    border-left: 2px solid gray;
    border-bottom: 2px solid gray;
    border-right: 2px solid #ccc;
}

.Monitor input[type='text']{
    /* width: 100%; */
    margin-bottom: .3em;
    margin-top: .3em;
}
.Monitor input[type='number']{
    width: 4em;
    
}
.Monitor .numbers{
    display: flex;
    /* flex-direction: column; */
    justify-content: space-between;
    box-sizing: border-box;
}

.Monitor .numbers b{
    /* padding-left: 1em; */
}

.Monitor .ponters{
    display: flex;
    justify-content: space-around;
    /* justify-content: space-between; */
}

.Monitor .pad{
    text-align: center;
}
.Monitor .pad > div{
    display: flex;
    justify-content: space-around;
}
.Monitor .pad > div svg{
    border: 1px solid gray;
    height: 2em;
    width: 2em;
}
